input {
  position: relative;
  display: none;
}

label {
  display: block;
  height:5rem;
  width:5rem;
  top:3rem;
  left:2rem;
  position: fixed;
  cursor: pointer;
  background: url('./moon.png');
  background-size:cover;
  z-index:99;
}

input:checked + label {
  background: url('./smileysun.png');
 background-size:cover; 
 transition: background 1s;
}


input:checked ~ #window {
  background-image: url('./finalb.png');
}



input:checked ~ #window > #hi1  {
  
  opacity: 0;
  transition: opacity 5s
}

input:checked ~ #window > #hi2  {
  
  color: #333;
  opacity: 1;
  font-family: "Gochi Hand", "Comic Sans", cursive;
  transition: opacity 5s;
  z-index:50;
}

input:checked ~ #window > .arrow span{
  border-bottom: 5px solid #333;
    border-right: 5px solid #333;
color: white;}

input:checked ~ #wrapper {
  color: #333;
  background-color:#F7F9FE;
}

/*these are basically reusable variables, SCSS*/
$width:100vw;
