/*this is the fade in on page load*/
.fade {
-webkit-animation: fade 3s;
animation: fade 5s;
-moz-animation: fade 3s;
-o-animation: fade 3s;
}

@keyframes fade {
  0% {opacity: 0.0} 
  25% {opacity: 0.3}
  50% {opacity: 0.6}
  75% {opacity: 0.8;}
  100%{opacity: 1}
    }




